import {
  Route,
  RouterProvider,
  createBrowserRouter,
  createRoutesFromElements,
} from "react-router-dom";
import { BaseLayout } from "./components/BaseLayout";
import { AboutPage } from "./pages/AboutPage";
import { FilmDetails, filmLoader } from "./pages/FilmDeatilsPage";
import { ErrorPage } from "./pages/ErrorPage";
import { HomePage } from "./pages/HomePage";
import { FilmShowPage } from "./pages/FilmShowPage";
import { ROUTES } from "./constants";
import { ProtectedRoute } from "./components/ProtectedRoute";
import { UserPage } from "./pages/UserPage";
import { Loader } from "./components/Loader";
import { FilmCritics } from "./components/FilmCritics";
import { FilmDirector } from "./components/FilmDirector";
import { StartShowPage, startShowAction } from "./pages/StartShowPage";

const router = createBrowserRouter( //     Data API
  createRoutesFromElements( // ,       
    <Route path="/" element={<BaseLayout />} errorElement={<ErrorPage />}> {/*  ,   */}

      {/*  ,  */}
      <Route index element={<HomePage />} /> {/* index   ,    - */}

      {/*  about */}
      <Route path="about" element={<AboutPage />} />

      {/*    ,   */}
      <Route
        path="films/:id" /* ":id"   ,    ,        Use Params.         ,      */
        element={<FilmDetails />} 
        loader={filmLoader}
      >
        <Route index element={<FilmDirector />} loader={filmLoader} /> {/*    */}
        <Route path="critics" element={<FilmCritics />} loader={filmLoader} /> {/*     */}
      </Route>

      {/*    */}
      <Route
        path="films/:id/start-show"  /* ":id"   ,    ,        Use Params.         ,      */
        element={<StartShowPage />}
        action={startShowAction} //  ,    -get  (post, put, patch, delete).   
      />

      {/*    */}
      <Route
        path="films"
        fallbackElement={<Loader />} // fallbackElement ( ) ,   
        lazy={() => //   lazy,          ,       
          import("./pages/FilmsPage").then((module) => ({ //    
            Component: module.FilmsPage,
            loader: module.filmsLoader,
          }))
        }
      />

      {/*   */}
      <Route
        path={ROUTES.user}
        element={
          /*  isAllowed={true},    http://192.168.0.101:3000/user    UserPage */
          <ProtectedRoute isAllowed={false}> 
            <UserPage />
          </ProtectedRoute>
        }
      />

    <Route path="/film-show/:id" element={<FilmShowPage />} />

      {/* C  ,     . "*" ,         */}
      <Route path="*" element={<ErrorPage />} /> 
    </Route>
  )
);

function App() {
  return <RouterProvider router={router} fallbackElement={<Loader />} />;
}

export default App;
