//   
import { Await, Outlet, defer, useLoaderData } from "react-router-dom";
import { mockFetch } from "../utils/api";
import { LinkButton } from "../components/LinkButton";
import { Tabs } from "../components/Tabs";
import { Suspense } from "react";
import { Loader } from "../components/Loader";

const TABS = [ // 
  {
    path: "", //   
    title: "About Film Director", 
  },
  {
    path: "critics", //       critics
    title: "Film Critics",
  },
];

export const filmLoader = ({ params: { id } }) => { //      params,     id
  const film = mockFetch(`/films/${id}`); //    
  return defer({ //    . Defer - ,    ,   ,     
    film,
  });
};

export const FilmDetails = () => {
  const { film } = useLoaderData(); //    .  film  ,    resolve

  return (
    <Suspense fallback={<Loader />}> 
      {/*  Await     ,  ,   .      React.Suspense  React.SuspenseList   fallback ( )     */}
      <Await resolve={film}> 
        {({ imageUrl, title, year, starring, description }) => ( 
          <div className="film-details">
            <div className="film-details_container">
              <div> 
                <img 
                  className="film-details_image"
                  src={imageUrl}
                  alt="film"
                /> {/*   */}
              </div>
              <div className="film-details_content">
                <div className="film-details_title">HORROR FILM</div> {/* Title */}
                <div className="film_title">{title} ({year})</div> {/*      */}
                <p className="film-details_starring">STARRING: {starring}</p> {/*   */}
                <p className="film-details_descr">DESCRIPTION: {description}</p> {/*   */}

                <div className="start-showing">
                  <LinkButton to="start-show" title="Start showing" /> {/*  */}
                </div>
              </div>
            </div>

            <div className="film-details_tabs">
              <Tabs tabs={TABS} /> {/*       */}
              <Outlet /> {/*   Tabs */}
            </div>

          </div>
        )}
      </Await>
    </Suspense>
  );
};
