//  :
import {
  Form,
  json,
  redirect,
  useActionData,
  useNavigation,
} from "react-router-dom";
import { mockFetch } from "../utils/api";
import { Loader } from "../components/Loader";

export const startShowAction = async ({ params: { id }, request }) => { //   - id    request,    
  const formData = await request.formData(); // await   
  console.debug(formData); //       ,       

  const filmDetails = await mockFetch(`/films/${id}`); //   ,   

  //   name
  if (!formData.get("name")) { //    
    return json({ message: "Name field can't be empty" }, { status: 400 }); //   . json -  ,     json-
  }

  alert(`${formData.get("name")}, welcome to show "${filmDetails.title}" film!`); //   "   "

  // return redirect(`/films/${id}`); //   (   redirect  "react-router-dom")    
  return redirect(`/film-show/${id}`); //      id 
};

export const StartShowPage = () => {
  const navigation = useNavigation(); // ,          navigate ,         .    ,      
  const data = useActionData(); // ,       
  return (
    <div className="start-show_container">
      <h1 className="start-show_title"> {/*  */}
        Start showing now!
      </h1>

      {navigation.state === "submitting" && <Loader />} {/*  ,   */}

      <Form method="post" className="start-show_form"> {/*  Form     post      action. Form -   for HTML-,     ,  HTML  HTTP  */}
        <div>
          <input //   
            className="form-input"
            type="text"
            name="name"
            placeholder="Enter your name"
          />
        </div>
        {data?.message && <p className="error-validation">{data.message}</p>} {/*     name */}
        
        <div>
          <input //   
            className="form-input"
            type="email"
            name="email"
            placeholder="Enter your email*"
          />
          
        </div>
        <p className="error-validation">*not reqired</p>
        
        <button className="button" type="submit">
          Start showing
        </button>
      </Form>
    </div>
  );
};
